import { Injectable, TemplateRef } from '@angular/core';
import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { ConsentsHttpService } from '@backbase/data-ang/consent';
import { BehaviorSubject, combineLatest, Observable, of, ReplaySubject } from 'rxjs';
import { catchError, distinctUntilChanged, filter, pluck, switchMap, take, tap } from 'rxjs/operators';

import { NotificationService } from '@backbase/ui-ang/notification';
import { ConsentDetails } from './model/types';
import { ConsentDetailsError, parseError } from './model/consent-details-error';

@Injectable()
export class ConsentDetailsService {
  private readonly consentId = new ReplaySubject<string>(1);
  private readonly revokingComplete = new BehaviorSubject<boolean>(true);

  readonly loading = new BehaviorSubject<boolean>(true);
  readonly error = new BehaviorSubject<ConsentDetailsError | undefined>(undefined);

  readonly consentDetails = combineLatest([this.consentId, this.revokingComplete]).pipe(
    filter(([id]) => !!id),
    distinctUntilChanged(),
    tap(() => this.loading.next(true)),
    switchMap(([id]) => this.getConsentDetailsById(id)),
    tap(() => this.error.next(undefined)),
    catchError((error: ConsentDetailsError) => {
      this.error.next(error);
      return of(undefined);
    }),
    tap(() => this.loading.next(false)),
  );

  constructor(
    private readonly consentDataService: ConsentsHttpService,
    private readonly notification: NotificationService,
  ) {}

  getConsentFrom(consentId: Observable<string>) {
    consentId.subscribe(this.consentId);
  }

  requestRevoke(
    errorTemplateRef: TemplateRef<any> | string,
    successTemplateRef: TemplateRef<any> | string,
  ): Observable<any | undefined> {
    return this.consentId.pipe(
      take(1),
      filter((id: string) => !!id),
      switchMap((id: string) => this.revokeConsentById(id)),
      tap(() => {
        this.revokingComplete.next(true);
        this.showNotification(successTemplateRef, 'success');
      }),
      catchError(() => {
        this.showNotification(errorTemplateRef, 'error');
        return of(undefined);
      }),
    );
  }

  private getConsentDetailsById(id: string): Observable<ConsentDetails> {
    return this.consentDataService.getIdByIdgetId({ id }, 'response').pipe(
      pluck<HttpResponse<ConsentDetails>, ConsentDetails>('body'),
      catchError<ConsentDetails, never>((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }

  private revokeConsentById(id: string): Observable<any> {
    return this.consentDataService.postRevokeById({ id }, 'response').pipe(
      catchError((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }

  private showNotification(templateRef: TemplateRef<any> | string, modifier: 'success' | 'error') {
    this.notification.showNotification({
      header: templateRef,
      message: '',
      modifier,
    });
  }
}
