import { Component, Directive, TemplateRef, ViewChild, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { Observable, combineLatest } from 'rxjs';
import { map, pluck } from 'rxjs/operators';

import { ConsentDetailsService } from '../consent-details.service';
import { ConsentDetails, ConsentDetailsContainerCustomizableContext } from '../model/types';
import { Status } from '../model/constants';
import { ConsentDetailsError } from '../model/consent-details-error';

@Component({
  selector: 'bb-consent-details-container',
  templateUrl: './consent-details-container.component.html',
})
export class ConsentDetailsContainerComponent implements OnInit {
  /**
   * Component context
   */
  readonly hostRef: ConsentDetailsContainerComponent = this;
  /**
   * Notification error template reference
   */
  @ViewChild('notificationError', { static: true })
  notificationErrorTemplate: TemplateRef<any> | string = '';
  /**
   * Notification success template reference
   */
  @ViewChild('notificationSuccess', { static: true })
  notificationSuccessTemplate: TemplateRef<any> | string = '';
  /**
   * Stream of loading spinner visibility flag
   */
  readonly loading: Observable<boolean> = this.detailsService.loading;
  /**
   * Stream of consent details request errors
   */
  readonly error: Observable<ConsentDetailsError | undefined> = this.detailsService.error;
  /**
   * Stream of consent ID
   */
  readonly consentId: Observable<string> = this.getRouteParam(this.route, 'consentId').pipe(
    map((consentId: string | null) => consentId || ''),
  );
  /**
   * Stream of consent details
   */
  readonly consentDetails: Observable<ConsentDetails | undefined> = this.detailsService.consentDetails;
  /**
   * Consent reject modal visibility flag
   */
  isModalOpen = false;
  /**
   * Consent status enumeration
   */
  readonly Status = Status;

  ngOnInit() {
    this.detailsService.getConsentFrom(this.consentId);
  }

  /**
   * Open consent reject modal
   */
  openRejectConsentModal(): void {
    this.isModalOpen = true;
  }

  /**
   * Close consent reject modal
   */
  dismissRejectConsentModal(): void {
    this.isModalOpen = false;
  }

  /**
   * Revoke consent
   */
  revokeConsent(): void {
    this.detailsService.requestRevoke(this.notificationErrorTemplate, this.notificationSuccessTemplate).subscribe();
    this.dismissRejectConsentModal();
  }

  private getRouteParam(route: ActivatedRoute, param: string): Observable<string | null> {
    const paramValue = route.paramMap.pipe(pluck<ParamMap, string | null>('params', param));

    if (!route.parent) {
      return paramValue;
    }

    return combineLatest([paramValue, this.getRouteParam(route.parent, param)]).pipe(
      map(params => params[0] || params[1]),
    );
  }

  constructor(private readonly detailsService: ConsentDetailsService, private readonly route: ActivatedRoute) {}
}

@Directive({
  selector: '[bbConsentDetailsContainerCustomizable]',
})
export class ConsentDetailsContainerCustomizableDirective extends BbTemplate<
  ConsentDetailsContainerComponent,
  ConsentDetailsContainerCustomizableContext
> {}
