import { HttpErrorResponse } from '@angular/common/http';

export class ConsentDetailsAccessDenied {
  kind: 'ConsentDetailsAccessDenied' = 'ConsentDetailsAccessDenied';
  constructor(readonly error: HttpErrorResponse) {}
}

export class ConsentDetailsNotFound {
  kind: 'ConsentDetailsNotFound' = 'ConsentDetailsNotFound';
  constructor(readonly error: HttpErrorResponse) {}
}

export class ConsentDetailsBadRequest {
  kind: 'ConsentDetailsBadRequest' = 'ConsentDetailsBadRequest';
  constructor(readonly error: HttpErrorResponse) {}
}

export class ConsentDetailsUnknownError {
  kind: 'ConsentDetailsUnknownError' = 'ConsentDetailsUnknownError';
  constructor(readonly error: HttpErrorResponse) {}
}

export class ConsentDetailsConnectivityError {
  kind: 'ConsentDetailsConnectivityError' = 'ConsentDetailsConnectivityError';
  constructor(readonly error: HttpErrorResponse) {}
}

export type ConsentDetailsError =
  | ConsentDetailsAccessDenied
  | ConsentDetailsNotFound
  | ConsentDetailsBadRequest
  | ConsentDetailsUnknownError
  | ConsentDetailsConnectivityError;

export const parseError = (error: HttpErrorResponse): ConsentDetailsError => {
  if (error.error instanceof ErrorEvent) {
    return new ConsentDetailsUnknownError(error);
  } else {
    switch (error.status) {
      case 0:
        return new ConsentDetailsConnectivityError(error);
      case 400:
        return new ConsentDetailsBadRequest(error);
      case 401:
        return new ConsentDetailsAccessDenied(error);
      case 403:
        return new ConsentDetailsAccessDenied(error);
      case 404:
        return new ConsentDetailsNotFound(error);
      default:
        return new ConsentDetailsUnknownError(error);
    }
  }
};
